<template>
	<div class="mine">
		<router-link tag="div" to="MineData" class="mine-data">
			<div class="header">
				<div class="icon">
					<img src="../assets/ico_header.png">
				</div>
				<div class="label">PRO</div>
			</div>
			<div class="nickname">他与柠檬</div>
			<div class="personalized-signature">此人没有填写个性签名~</div>
		</router-link>
		<ul class="mine-list">
			<li class="mine-items">
				<div class="items-title">上传作品</div>
				<div class="items-value"></div>
			</li>
			<li class="mine-items">
				<div class="items-title">申请成为认证设计师</div>
				<div class="items-value">已认证</div>
			</li>
			<li class="mine-items">
				<div class="items-title">我的作品</div>
				<div class="items-value"></div>
			</li>
			<li class="mine-items">
				<div class="items-title">我的订单</div>
				<div class="items-value"></div>
			</li>
			<router-link tag="li" to="MineCollect" class="mine-items">
				<div class="items-title">商品收藏</div>
				<div class="items-value"></div>
			</router-link>
			<li class="mine-items">
				<div class="items-title">优惠券</div>
				<div class="items-value"></div>
			</li>
			<li class="mine-items">
				<div class="items-title">收益</div>
				<div class="items-value"></div>
			</li>
			<router-link tag="li" to="MineSetting" class="mine-items">
				<div class="items-title">设置</div>
				<div class="items-value"></div>
			</router-link>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'Mine',
	data () {
		return {
			msg:"我的"
		}
	},
	created (){
		document.title="我的";
		window.scrollTo(0, 0);
	},
	mounted () {
		this.init()
	},
	methods: {
		init: function () {

		}
	}
}
</script>

<style scoped>
	.header{
		width: 8rem;
		height: 8rem;
		position: relative;
		overflow: hidden;
		margin:1.5rem auto;
	}
	.header .icon{
		border-radius: 8rem;
		overflow: hidden;
	}
	.header .label{
		position: absolute;
		right: 1rem;
		bottom: 0;
		background-color: #000;
		color: #fff;
		font-weight: bold;
		font-size: 0.75rem;
		padding: 0.2rem;
	}
	.nickname{
		text-align: center;
		font-size: 1.125rem;
	}
	.personalized-signature{
		color: #666;
		font-size: 0.875rem;
		text-align: center;
		margin: 1rem;
	}
	.mine-list{
		padding: 2rem;
		color: #666;
	}
	.mine-items{
		border-bottom: 1px solid #e5e5e5;
		background: url("../assets/ico-right.png") right no-repeat;
		background-size: auto 0.75rem;
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		padding: 1rem 1rem 1rem 0;
	}
	.items-value{
		color: #999;
	}
</style>



